


















































































































































































































.movie-header {
  position: relative;
  height: 366px;
  box-sizing: border-box;
  padding: 30px;
  filter: blur(0);
  overflow: hidden;
}

.movie-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  filter: blur(30px);
  z-index: -1;
}

.movie-mask {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #40454e;
  opacity: 0.55;
}

.movie-content {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  color: #fff;
}

.movie-poster {
  width: 214px;
  height: 300px;
  background-size: cover;
  background-repeat: no-repeat;
  border: solid 1px #f0f2f3;
}

.movie-info {
  height: 300px;
  width: 60%;
  margin-left: 30px;
  padding-top: 10px;
  font-size: 22px;
  color: #e5e5e5;
}

.movie-name {
  font-size: 36px;
  font-weight: 600;
  color: #fff;
}

.movie-ename {
  color: #fff;
  margin-bottom: 12px;
}

.movie-score {
  color: #ffc600;
  font-size: 36px;
}

.movie-star {
  width: 36px;
  height: 36px;
  margin-right: 6px;
}

.score-num {
  font-size: 20px;
}

.no-score {
  font-size: 32px;
}

.movie-category {
  display: flex;
  align-items: center;
  margin-top: 12px;
}

.tag-box {
  color: #fff;
  border: 0.02rem solid rgba(255, 255, 255, 0.4);
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 20px;
  margin-left: 4px;
}

.tag-d {
  padding: 0 4px;
  background-color: rgba(255, 255, 255, 0.4);
}

.tag-imax {
  padding: 0 4px;
}

.movie-body {
  margin-bottom: 110px;
}

.section {
  padding-bottom: 20px;
  border-bottom: 20px solid #f5f5f5;
  color: #555;
}

.section-title {
  padding: 20px 30px 10px;
  font-size: 32px;
  color: #444;
}

.synopsis {
  padding: 0 30px;
  line-height: 1.42;
  font-size: 30px;
}
.hidden {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  text-overflow: ellipsis;
  word-wrap: break-word;
  word-break: break-all;
  overflow: hidden;
}
.icon-jiantouarrow483 {
  height: 44px;
  width: 50px;
  background: red;
  text-align: center;
  font-size: 32px;
  line-height: 44px;
}

.unfold {
  transform: rotate(180deg);
}
.icon-xiala {
  text-align: center;
  font-size: 36px;
}

.scroll-view_H {
  padding: 0 30px;
  font-size: 30px;
  box-sizing: border-box;
}

.videoImg-box {
  position: relative;
  display: inline-block;
}

.icon-zanting {
  position: absolute;
  bottom: 15px;
  right: 20px;
  font-size: 40px;
  opacity: 0.7;
  color: #fff;
}

.videoImg {
  width: 330px;
  height: 180px;
  margin-right: 10px;
}

.photo {
  display: flex;
  img {
    height: 180px;
    width: 250px;
    margin-left: 10px;
    &:last-child {
      padding-right: 10px;
    }
  }
}

.purchase {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background: #e54847;
  color: #fff;
  font-size: 40px;
  line-height: 100px;
  text-align: center;
}

.comment {
  padding: 20px 30px;
  border-bottom: 1px solid #e6e6e6;
}

.total {
  height: 80px;
  line-height: 80px;
  font-size: 30px;
  color: #f63;
  text-align: center;
  border-top: 1px solid #e6e6e6;
  display: block;
}
